* {
    margin: 0;
    padding: 0;
}

.scroll-container {
  height: 100vh;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;  /* Enable vertical scroll snapping */
  scroll-behavior: smooth;  /* Smooth scrolling between sections */
}

.heading {
    font-size: 2em;
}

.page1, .project1, .project2, .project3, .project4, .education, .footnotes {
  height: 100vh;  /* Make each section the full viewport height */
  scroll-snap-align: start;  /* Snap to the start of each section */
}

.page1 {
    margin: 0;
    height: 100vh;
    background-image: url('/Users/cory_boy/Documents/React-app/portfolio-website/public/background.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
}

.profile {
    border: 1px solid #f0f0f0;
    border-radius: 50%;
    width: 20%;
    height: 20%;
    object-fit: cover;
}

.about_me_wrap {
    display: flex;
}

.logo {
  width: 5%;
  height: auto;
  position: absolute;
  left: 95%;
}

.logo:hover {
  filter: brightness(0) invert(1);
  cursor: pointer;
}

.linkedin_logo, .github_logo {
    width: 2%;
    height: auto;
    filter: brightness(0) invert(1);
    margin-left: 3%;
    margin-top: 1%;
}

.linkedin_logo:hover, .github_logo:hover {
    filter: brightness(1) invert(0);
}

.git_link1 {
    display: inline-block;
    font-size: 1em;
    color: #28282B;
    font-family: Courier New;
    padding: 5px;
}

.git_link2 {
    display: inline-block;
    font-size: 1em;
    color: whitesmoke;
    font-family: Courier New;
    padding: 5px;
}

.env_links, .speed_links {
    text-align: right;
    margin-right: auto;
    position: relative;
    left: 3%;
}

.wassgood_links {
    position: absolute;
    right: 3%;
}

.content {
    font-family: Courier New;
    font-size: 4vh;
    text-align: center;
    color: #ffffff;
    padding-top: 200px;
}

.text11_wrap, .text31_wrap {
    position: absolute;
    padding: 40px;
}

.text11, .text31 {
    font-family: Courier New;
    color: #28282B;
    text-align: left;
    width: 75%;
    margin-right: auto;
}

.text21 {
    font-family: Courier New;
    color: whitesmoke;
    text-align: right;
    width: 75%;
    margin-right: auto;
}

.about_me {
    text-align: left;
    padding: 5%;
    font-size: 2vh;
    width: 60%;
    align-content: center;
}

.wassgood {
    width: 42%;
    height: auto;
    position: absolute;
    height: auto;
    bottom: 12%;
    right: 25%;
    margin-left: 25px;
}

.wassgood_footnote {
    font-family: Courier New;
    color: #33ff00;
    position: absolute;
    right: 5%;
    bottom: 5%;
}

.speed_footnote {
    font-family: Courier New;
    color: #2E6F40;
    position: absolute;
    right: 5%;
    bottom: 5%;
}

.down-button1 {
    border: none;
    background-color: rgba(255, 255, 255, 0);
    color: whitesmoke;
    font-size: 1em;
    cursor: pointer;
    transition: background-color 0.3s;
}

.down-button2 {
    border: none;
    position: absolute;
    background-color: rgba(255, 255, 255, 0);
    color: black;
    font-size: 2em;
    cursor: pointer;
    transition: background-color 0.3s;
    left: 50%;
    bottom: 5%;
}

.down-button3 {
    border: none;
    position: absolute;
    background-color: rgba(255, 255, 255, 0);
    color: whitesmoke;
    font-size: 2em;
    cursor: pointer;
    transition: background-color 0.3s;
    left: 50%;
    bottom: 5%;
}

.down-button1:hover,  .down-button3:hover, .profile_header:hover{
    color: black;
}

.down-button2:hover {
    color: whitesmoke;
}

.project1, .project3, .education {
    height: 100vh;
    background-color: #f0f0f0;
    position: relative;
}

@media (min-width: 600px) {
    .nav_links, .gitLink {
        display: inline-block;
    }
}

@media (max-width: 600px) {
    .project1, .project2, .project3, .about_me_wrap {
        display: flex;
        flex-direction: column;
        padding: 0;
        margin: 0;
        align-items: center;
    }

    .heading1, .heading2, .heading3 {
        font-size: 1.5em !important;
        position: static;
        padding: 0;
        margin: 0;
    }

    .text11, .text21, .text31 {
        width: 100%;
        padding: 10px;
        position: static;
        text-align: left;
    }

    .text11_wrap, .text21_wrap, .git_link1, .git_link2, .text31_wrap, .wassgood_footnote, .speed_footnote{
        position: static;
        padding: 5px;
    }

    .down-button2, .down-button3, .down-button4{
        display: none;
    }

    .profile {
        width: 50% !important;
        height: auto !important;
        max-width: 200px !important;
        align-content: center !important;
    }

    .about_me {
        width: 90%;
        position: static !important;
    }

    .university {
        position: static !important;
        font-size: 0.8em !important;
    }

    .msu_logo {
        position: static !important;
        width: 40px;
        height: auto;
    }

    .univ_cred {
        position: static !important;
        padding-bottom: 30px !important;
    }

    .env_links, .wassgood_links, .speed_links {
        padding-left: 20px;
        position: static;
    }

    .speed_img1 {
        max-height: 5% !important;
        width: auto !important;
        position: static !important;
    }

    .speed_img2 {
        max-height: 5% !important;
        width: auto !important;
        position: static !important;
    }

    .env_img{
        max-height: 25% !important;
        width: auto !important;
        position: static !important;
    }

    .wassgood {
        display: none;
    }
}

.project2, .project4 {
    height: 100vh;
    background-color: #28282B;
    position: relative;
}

.heading1, .heading3 {
    text-align: left;
    padding: 20px;
    font-family: Courier New;
    font-size: 6vh;
}

.heading2, .heading4 {
    text-align: right;
    padding: 30px;
    font-family: Courier New;
    font-size: 6vh;
    color: #f0f0f0;
}

.footnotes {
    position: relative;
    height: 100vh;
    width: auto;
    background-color: #f0f0f0;
}

.footnotes_image {
    position: absolute;
    bottom: 0;
    margin: 0;
    width: 100%;
    height: auto;
}

.prog_logo {
    width: auto;
    height: 70%;
    padding: 20px;
}

.env_img {
    position: absolute;
    bottom: 12%;
    width: 40%;
    height: auto;
    padding-left: 15%;
}

.speed_img1 {
    position: absolute;
    height: auto;
    width: 20%;
    right: 15%;
    bottom: 15%;
}

.speed_img2 {
    position: absolute;
    height: auto;
    width: 40%;
    left: 5%;
    bottom: 12%;
}

.university, .tech_stack {
    padding: 20px;
    display: flex;
    margin-left: 10px;
}

.msu_logo {
    height: 10vh;
    width: auto;
    padding-left: 25px;
    padding-top: 35px;
}

.univ_cred {
    display: flex;
    position: absolute;
    bottom: 5%;
}


